/* 공통 */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  color: #333333;
}

/* 버튼 */
.btn {
  border: 1px solid #cccccc;
  padding: 5px 8px;
  margin: 0 5px 20px;
  cursor: pointer;
}

/* 섹션 */
.kurly {
  width: 1400px;
  margin: 0 auto;
  padding-top: 40px;
  text-align: center;
  border-top: 1px solid #cccccc;
}

h2 {
  margin-bottom: 40px;
}

/* 이미지 서식 */
.imgBox {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  margin: 0 auto 40px;
}

.imgBox>li {
  width: calc(100% / 4 - 20px);
  margin: 0 10px;
  text-align: left;
}

.imgBox>li>p {
  margin-bottom: 8px;
}

img {
  width: 100%;
}

.cart {
  padding: 10px;
  border: 1px solid #cccccc;
  text-align: center;
}

.product {
  height: 40px;
  font-weight: bold;
  font-size: 18px;
}

.priceBox {
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  font-weight: bold;

}

.sale {
  color: #ff0000;
}

.hidden {
  display: none;
}
