.login {
  width: 400px;
  margin: 0 auto;
  text-align: center;
}

.loginForm {
  fieldset{
    border: none;
    legend{
      font: {
        size: 24px;
        weight: bold
      }
    }
    .contBox{
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 15px 0;
      input{
        width: 320px;
        height: 40px;
        padding: 10px;
      }
    }
    input[type=submit]{
      width: 100%;
      height: 40px;
      margin: 10px 0 20px;
      background-color: purple;
      border: none;
      border-radius: 5px;
      color: #ffffff;
    }
  }
}

.linkTo{
  display: flex;
  justify-content: space-around;
}

.result{
  text-align: right;
  margin: 20px 0;
}
